import React, { useState, useEffect } from "react";
import { Table, Input, Button, Space } from "antd";
import Highlighter from "react-highlight-words";

export function OverviewTable(props) {
  const [data, setData] = useState();
  const [filteredData, setFilteredData] = useState();

  useEffect(() => {
    // Der Pfad zur JSON-Datei
    const jsonFilePath = "csvjson.json";

    fetch(jsonFilePath)
      .then((response) => response.json())
      .then((jsonData) => {
        if (jsonData.length > 0) {
          // Die Spaltennamen aus dem ersten Datensatz extrahiere
          setData(jsonData);
        } else {
          console.error("JSON data is empty or invalid.");
        }
      })
      .catch((error) => {
        console.error("Error fetching JSON data:", error);
      });

    if (data) {
      // Filtere die Daten mit den hartcodierten Filtern
      const filtered = data.filter((item) => {
        return item.Type === props.anforderungsFilter.Type;
      });
      //console.log("Filtere  " + filtered.length);
      setFilteredData(filtered);
    }
  }, [data]);

  // Validiere, ob data vorhanden ist, bevor du versuchst, zu filtern

  const columns = [
    {
      title: "Name",
      dataIndex: "Name",
      key: "Name",
      width: 150,
    },
    {
      title: "Mglichkeiten",
      dataIndex: "Erweiterung",
      key: "Erweiterung",
      width: 150,
    },
    {
      title: "Action",
      dataIndex: "Action",
      key: "Action",
      width: 200,
    },
    {
      title: "Domne",
      dataIndex: "Tool",
      key: "Tool",
      width: 150,
    },
    // Weitere Spalten hinzufgen und die Breiten nach Bedarf anpassen
  ];

  return <Table dataSource={filteredData} columns={columns} />;
}
